import React from 'react';
import '../css/women.css';
import {Divider} from 'antd'
const Women = ({show,hide}) => {
    if(show){
        return(
            <div>
                <div className="ct animated fadeIn faster bg-white" onMouseLeave={hide}>
                <div className="bg-green  ">CLOTHING</div>
                <div className="bg-yellow">SHOES</div>
                <div className="bg-pink">ACCESORIES</div>
                <div className="bg-red">PHOTOS</div>
            </div>
            <Divider className="ma0"/>
            </div>
        )
    }
    else{
        return null;
    }
   
}

export default Women;