import React from 'react';
import '../css/men.css';
import {Divider} from 'antd';

const Men = ({show,hide}) => {
    if(show){
        return(
            <div>
                <div className="men_ct animated fadeIn faster bg-white" onMouseLeave={hide}>
                <div className="bg-green  ">CLOTHING</div>
                <div className="bg-yellow">SHOES</div>
                <div className="bg-pink">ACCESORIES</div>
                <div className="bg-red">PHOTOS</div>
            </div>
            <Divider className="ma0"/>
            </div>
        )
    }
    else{
        return null;
    }
   
}

export default Men;