import React from 'react';
import '../css/navcategory.css';
import {Divider} from 'antd'

const NavCategory = ({show,hide}) => {
   if(show){
    return(
        <div>
            <div className="navcat animated fadeIn faster bg-white" onMouseLeave={hide}>
            <div className="bg-red">MEN COLLECTION</div>
            <div className="bg-orange">WOWEN COLLECTION</div>
            <div className="bg-green">ACCESSORIES</div>
            <div className="bg-pink">FEATURED PRODUCTS</div>
            <div className="bg-purple"> TOP BRANDS</div>
        </div>
        <Divider className="ma0"/>
        </div>
    )
   }
   else{
       return null;
   }
}

export default NavCategory;