import React,{Component}from 'react';
import '../css/navbar.css';
import Sticky from 'react-sticky-el';
import '../css/Animate.css';
import '../media/navbar_media.css';
import SearchDrawer from '../components/SearchDrawer';
import Women from '../components/Women';
import Men from '../components/Men';
import NavCategory from '../components/NavCategory'
import { Divider, Icon,Popover,Input,Select } from 'antd';

const { Option } = Select;
const InputGroup = Input.Group;


const help = (
	<div>
		<div>About Us</div>
		<div>Customer Service</div>

	</div>
)
export default class Navbar extends Component{
	
	
		state={
			visible:false,
			show:false,
			menShow:false,
			catShow:false,
		}
	
	onClose = ()=>{
		this.setState({visible:false})
	}
	openSearchDrawer = () =>{
		this.setState({visible:true})
	}
	doShow = () =>{
		this.setState({show:true,menShow:false})
	}
	hideShow = () =>{
		this.setState({show:false})
	}
	doMenShow = () => {
		this.setState({menShow:true,show:false})
	}
	hideMenShow = () => {
		this.setState({menShow:false,catShow:false})
	}
	doCatShow = () => {
		this.setState({catShow:true})
	}
	hideCatShow = () => {
		this.setState({catShow:false})
	}
	render(){
		return (
			<nav className=" hparent  ">
				<SearchDrawer visible={this.state.visible} onClose={this.onClose}/>
				{/* <SearchDrawer visible={this.state.visible} onClose={this.onClose}/> */}
				<div className="  hchild flex justify-center items-center ">
					<div className=" w-90  flex flex-row h2">
						<div className="h2 width justify-between flex flex-row items-center  self-start black ">
							<div className="show ">
							   <Icon type="menu-unfold" style={{fontSize:'17px'}} />
							</div >
							<div className=" h-75">
							 <p style={{fontFamily: 'Fruktur',fontSize:'20px'}}>Grabit</p>
							</div>
							
						</div>
						<div className="h2 w1 justify-between flex ml-auto w-30 flex-row ">
						<div className="  self-center" >
							<Icon type="question-circle" />
							</div>
							<div className="  self-center" >
							<Icon type="user" />
							</div>
							<div className="  self-center" >
							<Icon type="heart" />
							</div>
							<div className="  self-center">
							<Icon type="shopping-cart" />
							</div>
							<div className=" self-center relative vanish">
							<InputGroup compact className="">
								<Select defaultValue="UGX(Shs)">
									<Option value="Option1">{"USD($)"}</Option>
		 							<Option value="Option2">{"UGX(Shs)"}</Option>
								</Select>
								</InputGroup>
							</div>
						</div>
					</div>
				</div>
				<Divider className="ma0 relative z-1" />
				<Sticky topOffset={83} stickyClassName={'animated fadeIn ontop hidden'}>
					<div className=" hchild2 h2  flex justify-center items-center  bg-white-80 transparent ">
						<div className=" w-90 flex flex-row h2">
							<div className="h2 w3  items-center justify-between flex self-start w-50 flex-row " >
							<div className="pointer hover-black-90" onMouseEnter={this.hideCatShow}>
									<p
										style={{
											fontSize: '11px',
											verticalAlign: 'middle',
											textAlign: 'center',
											display: 'table-cell',
										}}
									>
										HOME
									</p>
								</div>
								<div className="pointer hover-black-90 flex  flex-row" onMouseEnter={this.doCatShow}>
									<div className="items-center ">
									<p
										style={{
											fontSize: '11px',
											verticalAlign: 'middle',
											textAlign: 'center',
											display: 'table-cell',
											 paddingRight:'3px'
										}}
									>
										CATEGORIES
									</p>
									</div>
									<div className=" flex to  ml-auto spin1 fw1">
										<Icon type="down" style={{ fontSize: '8px',paddingTop:'5px' }} />
									</div>
									
								</div>
								<div className="pointer hover-black-90" onMouseEnter={this.hideMenShow}>
									<p
										style={{
											fontSize: '11px',
											verticalAlign: 'middle',
											textAlign: 'center',
											display: 'table-cell',
										}}
									>
										BEST SELLERS
									</p>
								</div>
								<div className="pointer hover-black-90 flex flex-row  h2 pt2" onMouseEnter={this.doMenShow} >
								<div className="items-center ">
									<p
										style={{
											fontSize: '11px',
											verticalAlign: 'middle',
											textAlign: 'center',
											display: 'table-cell',
											 paddingRight:'3px'
										}}
									>
										MEN'S
									</p>
									</div>
									<div className=" flex to  ml-auto spin1 fw1 ">
										<Icon type="down" style={{ fontSize: '8px',paddingTop:'5px' }} />
									</div>
								</div>
								<div className="pointer hover-black-90 flex  h2 pt2 flex-row" onMouseEnter={this.doShow} >
								<div className="items-center ">
									<p
										style={{
											fontSize: '11px',
											verticalAlign: 'middle',
											textAlign: 'center',
											display: 'table-cell',
											 paddingRight:'3px'
										}}
									>
										WOMEN'S
									</p>
									</div>
									<div className=" flex to  ml-auto spin1 fw1">
										<Icon type="down" style={{ fontSize: '8px',paddingTop:'5px' }} />
									</div>
								</div>
								<div className="pointer hover-black-90  h2 pt2" onMouseEnter={this.hideShow}>
									<p
										style={{
											fontSize: '11px',
											verticalAlign: 'middle',
											textAlign: 'center',
											display: 'table-cell',
										}}
									>
										SELL
									</p>
								</div>
								<Popover placement="bottom" content={help}>
								<div className="pointer hover-black-90">
									<p
										style={{
											fontSize: '11px',
											verticalAlign: 'middle',
											textAlign: 'center',
											display: 'table-cell',
										}}
									>
										HELP
									</p>
								</div>
								</Popover>
							</div>
							<div className="h2 w3  flex flex-row ml-auto items-center pointer hover-black-10" onClick={this.openSearchDrawer}>
								<div className="  items-center">
									<p
										style={{
											fontSize: '11px',
											verticalAlign: 'middle',
											textAlign: 'center',
											display: 'table-cell',
										}}
									>
										SEARCH
									</p>
								</div>
								<div className="ml-a flex flex-row ml-auto fw1 ">
									<Icon type="search" style={{ fontSize: '15px', color: '#141414' }} />
								</div>
							</div>
						</div>
					</div>
					<Divider className="ma0" />
					<Women show={this.state.show}  hide={this.hideShow}/>
				<Men show={this.state.menShow} hide={this.hideMenShow}/>
				<NavCategory show={ this.state.catShow} hide={this.hideCatShow}/>
				</Sticky>
				
			</nav>
		);
	}
};